.cases {
	position: relative;
	padding: 15vh 30px 310px 50px;
	padding-top: calc(100px + (180 - 100)*((100vh - 750px)/(960 - 750)));
	height: 100%;
	max-height: 100%;
	width: 595px;
	@include css-lock(14,20,1024,1440);
	&__content {
		max-width: 402px; }
	&__title {
		margin-bottom: 3vh;
		max-width: 275px; }
	&__slider {
		position: absolute;
		left: 364px;
		bottom: 0;
		width: calc(100vw - ((100vw - 1440px) / 2 + 364px) ); }
	&__slider-wrap {
		padding-top: 80px;
		overflow: hidden; } }
.slider {
	width: 600px;
	font-size: 0;
	.slick-list {
		overflow: visible; }
	&__prev,
	&__next {
		position: absolute;
		top: -80px;
		width: 75px;
		height: 80px;
		text-align: center;
		background-color: red;
		.icon {
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -9px 0 0 -5px;
			font-size: 18px; } }
	&__prev {
		left: 155px;
		color: $white;
		background-color: $black; }
	&__next {
		left: 230px;
		color: $black;
		background-color: $white; }
	// .slick-slide
	// 	width: 230px !important
	// 	overflow: hidden
	// 	&.slick-current
 }	// 		width: 600px !important
.project {
	width: 600px;
	// +font-smoothing
	table-layout: fixed;
	background-color: $black;
	display: table;
	&__img {
		width: 230px;
		height: 300px;
		vertical-align: top;
		background-position: 50% 50%;
		background-size: cover;
		display: table-cell;
		@include css-lock(150,300,1024,1440,height); }
	&__content {
		margin-bottom: 40px;
		padding: 20px 25px 45px;
		color: rgba($white, .5);
		letter-spacing: -0.02em;
		font-size: 16px;
		text-align: center;
		line-height: 1.625;
		vertical-align: middle;
		display: table-cell; }
	&__title {
		margin-bottom: 12px;
		font-family: $Futuras;
		@include f(20px,500,1.5);
		color: $white;
		letter-spacing: 0;
		text-transform: uppercase; } }
