.popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 6;
  padding: 30px;
  visibility: hidden;
  overflow: auto;
  text-align: center;
  font-size: 0;
}

.popup:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.popup::-webkit-scrollbar {
  width: 5px;
}

.popup::-webkit-scrollbar-thumb {
  background-color: #3D3A41;
}

.popup.is-active {
  visibility: visible;
}

.popup__overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  opacity: 0;
  background-color: rgba(25, 25, 38, 0.3);
}

.popup__inner {
  position: relative;
  width: 100%;
  max-width: 1200px;
  overflow: hidden;
  vertical-align: middle;
  display: inline-block;
}

.popup__close {
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  color: #fff;
  font-size: 0;
  z-index: 1;
  opacity: 0;
  background-color: #191926;
}

.popup__close .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -9px 0 0 -9px;
  font-size: 18px;
}

.popup__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform: scaleY(0);
}

.popup__triangle {
  position: absolute;
  top: 60%;
  left: 51.5%;
  opacity: 0.2;
  stroke-dasharray: 2500;
  stroke-dashoffset: 2500;
}

.popup__triangle-blur {
  top: 50%;
  left: 59%;
  opacity: 0.1;
}

.popup__triangle-blur svg {
  transform: rotate(180deg);
  stroke-dasharray: 2500;
  stroke-dashoffset: 2500;
}

.popup__triangle, .popup__triangle-blur {
  position: absolute;
  transform: translate(-50%, -50%);
}

.popup__content {
  position: relative;
  padding: 100px 50px 180px;
  padding-top: 100px;
  padding-top: calc(30px + (100 - 30) * ((100vw - 1024px) / (1440 - 1024)));
  padding-bottom: 180px;
  padding-bottom: calc(30px + (180 - 30) * ((100vw - 1024px) / (1440 - 1024)));
  color: #3D3A41;
  font-size: 20px;
}

