.fp-slider {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: 3;
	overflow: hidden;
	.inner {
		height: 100%; } }

[data-fp-slide] {
	position: absolute;
	top: 0;
	left: 0;
	padding-top: 86px;
	height: 100%;
	width: 100%;
	opacity: 0;
	visibility: hidden;
	transition: visibility .4s, opacity .4s;
	// text-align: center
	// font-size: 0
	&.is-active {
		visibility: visible;
		opacity: 1; } }
